        </Form-item>
        <myarea ref="myarea" @select="myareaOut" :address="address"></myarea>
        <form-item prop="addrStreet" :label-width="100" label="" style="margin-left: -10px">
          <Input  v-model="newOrg.addrStreet" placeholder="" class="grey" />
        </form-item>
        <Row :gutter="8">
          <iCol span="12">
            <Form-item prop="defaultPid" label="">
              <Select v-model="newOrg.defaultPid" placeholder="" size="small" class="grey" style="width: 188px;">
                <Option v-for="platform in platformList" :value=platform.defaultPid>{{platform.platformName}}</Option>
              </Select>
            </Form-item>
          </iCol>
          <iCol span="12">
            <Form-item prop="contactNum" label="">
              <Input type="text" class="grey" placeholder=""   v-model="newOrg.contactNum" style="width:188px"/>
            </Form-item>
          </iCol>
        </Row>
        <Row :gutter="8">
          <iCol span="12">
            <Form-item prop="ownerName" label="">
              <Input type="text" class="grey" placeholder=""  v-model="newOrg.ownerName" style="width:188px"/>
            </Form-item>
          </iCol>
          <iCol span="12">
            <Form-item prop="ownerTel" label=""  style="margin-left: 3px;">
              <Input type="text" class="grey" placeholder="" v-model="newOrg.ownerTel" style="width:188px;"/>
            </Form-item>
          </iCol>
        </Row>
        <Row :gutter="8">
          <iCol span="11">
            <Form-item prop="businessType" label="">
              <Select v-model="newOrg.businessType" placeholder="" size="small" style="width: 188px;" class="grey business-type-select">
                <Option v-for="fireUnitType in fireUnitTypeList" :value=fireUnitType.codeId style="overflow:hidden; text-overflow: ellipsis;">{{fireUnitType.codeValue}}</Option>
              </Select>
            </Form-item>
          </iCol>
          <iCol span="13">
            <Form-item prop="supervLevel" label="" :labelWidth="118" style="margin-left: 3px;">
              <Select v-model="newOrg.supervLevel" placeholder="" size="small" class="grey" style="width: 188px;">
                <Option v-for="supervision in supervisionLevelList" :value=supervision.codeId>{{supervision.codeValue}}</Option>
              </Select>
            </Form-item>
          </iCol>
        </Row>
      </Form>
      <div slot="footer">
        <Button class="grey cancel" @click="addOrgModal = false"></Button>
        <Button class="grey" @click="addOrg('newOrgFrom')"></Button>
      </div>
    </Modal>
