</template>
<script>
  import niceScroll from 'jquery.nicescroll';
  import Vue from 'vue';
  import server from '../config/api'
  import myArea from '../components/vue-myarea/area.vue';
  import {tryMove} from "../libs/modalMove.js";

  export default {
    data(){
      return {
        orgList: [],
        addOrgModal: false,
        province: [],
        city: [],
        district: [],
        street: [],
        newOrg: {
          uid: localStorage.access_user_id
        },
        address: {},
        newOrgValidate: {
          name: [
            { required: true, message: '', trigger: 'blur' }
          ],
          addrProv: [
            {required: true, message: '', trigger: 'blur'}
          ],
          addrCity: [
            {required: true, message: '', trigger: 'blur'}
          ],
          addrDistrict: [
            {required: true, message: '', trigger: 'blur'}
          ],
          addrStreet: [
            {required: true, message: '', trigger: 'blur'}
          ],
        },
        supervisionLevelList: [],
        fireUnitTypeList: [],
        platformList: []
      };
    },
    components: {
      'myarea': myArea,
    },
    methods: {
      myareaOut: function(d) {
        this.address = d;
        console.log(this.address);
      },
      getPlatformLogo: function() {
        if(this.orgList === null || this.orgList === undefined || this.orgList.length === 0) {
          this.$store.commit('setCurrentPlatform', {});
          return;
        }
        for(let index in this.orgList) {
          if(_.findIndex(this.platformList, {defaultPid: this.orgList[index].defaultPid}) === -1) {
            this.platformList.push({
              defaultPid: this.orgList[index].defaultPid,
              platformName: this.orgList[index].platformName
            });
          }
        }
        if(this.platformList.length > 1) {
          // logo
          this.$store.commit('setCurrentPlatform', {});
        } else {
          // logo
          this.$store.commit('setCurrentPlatform', {
            platformLogo: this.orgList[0].platformLogo === null ? null
              : server.api.img.replace('{imageId}', this.orgList[0].platformLogo),
            platformName: this.orgList[0].platformName
          });
        }
      },
      toOrg: function(org) {
        localStorage.default_pid = org.defaultPid;
        localStorage.current_org_id = org.orgId;
        localStorage.current_org_name = org.name;
        // logo
        this.$store.commit('setCurrentPlatform', {
          platformLogo: org.platformLogo === null ? null : server.api.img.replace('{imageId}', org.platformLogo),
          platformName: org.platformName
        });
        this.$http.get(server.api.user.auth.replace('{uid}', localStorage.access_user_id) + '?orgId=' + localStorage.current_org_id + '&level=ORG').then((response) => {
          this.$store.commit("setUserAuthList", response.body.data);
          setTimeout(() => {
            this.$router.push("/monitor");
          }, 1000);
        }, (error) => {
          this.$Message.error("");
        });
      },
      addOrg:function (name) {
        this.newOrg.addrProv=this.address.addrProv;
        this.newOrg.addrCity=this.address.addrCity;
        this.newOrg.addrDistrict=this.address.addrDistrict;
        this.$refs[name].validate((valid) =>{
          if (valid) {
            this.$http.post(server.api.organization.add, this.newOrg).then((response)=>{
              if(response.body.meta.success) {
                this.$Message.success("");
                if(this.orgList === undefined || this.orgList === null) {
                  this.orgList = [];
                }
                this.orgList.push(response.body.data);
                this.addOrgModal = false;
              } else {
                this.$Message.error("");
              }
            },(error) => {
              this.$Message.error('');
            })
          } else {
            this.$Message.error('!');
          }
        })
      },
//      saveNewOrg: function() {
//        if(!this.checkName()) {
//          return;
//        }
//        if(!this.checkAddr()) {
//          return;
//        }
//        this.newOrg.addrProv  = this.address.addrProv;
//        this.newOrg.addrCity = this.address.addrCity;
//        this.newOrg.addrDistrict  = this.address.addrDistrict ;
//        this.$http.post(server.api.organization.add, this.newOrg).then((response) => {
//          if(response.body.meta.success) {
//            this.$Message.success("");
//            if(this.orgList === undefined || this.orgList === null) {
//              this.orgList = [];
//            }
//            this.orgList.push(response.body.data);
//            this.addOrgModal = false;
//          } else {
//            this.$Message.error("");
//          }
//        }, (error) => {
//          this.$Message.error("");
//        });
//      },
      orgOut: function(org) {
        this.$Modal.confirm({
          content: ' ['+org.name+'] ',
          okText: '',
          onOk: () => {
            this.toOut(org);
          }
        });
      },
      toOut: function(org) {
        this.$http.post(server.api.organization.quit, {
          uid: localStorage.access_user_id,
          orgId: org.orgId
        }).then((response) => {
          if(!response.body.data) {
            this.$Message.error("");
            return;
          }
          this.$Message.success("");
          this.orgList.splice(_.findIndex(this.orgList, {orgId: org.orgId}), 1);
        }, (error) => {
          this.$Message.error("");
        });
      },
      loadSystemCode: function () {
        let categories=[];
        categories.push('SUPERVISION_LEVEL');
        categories.push('FIRE_UNIT_TYPE');
        this.$http.get(server.api.organization.sysCode.getSysCode+'?categories[]='+categories).then((response)=>{
          let systemCodeList = response.body.data;
          this.supervisionLevelList = _.filter(systemCodeList, {'codeCategory': 'SUPERVISION_LEVEL'});
          this.fireUnitTypeList = _.filter(systemCodeList, {'codeCategory': 'FIRE_UNIT_TYPE'});
        },(error)=>{
          this.$Message.error("");
        })
      },
    },
    watch: {
      addOrgModal: function(val, oldVal) {
        if(val) {
          this.newOrg = {};
          this.newOrg.uid = localStorage.access_user_id;
          this.newOrg.defaultPid = this.platformList.length === 0 ? null : this.platformList[0].defaultPid;
          this.$refs.newOrgFrom.resetFields();
          this.address = {};
          Vue.nextTick(() => {
            this.$refs.myarea.initData();
          });
          if(this.supervisionLevelList.length === 0 && this.fireUnitTypeList.length === 0) {
            // systemCode
            this.loadSystemCode();
          }
        }
      }
    },
    mounted(){
      this.orgList = this.$store.state.org_list;
      this.getPlatformLogo();
      tryMove();
      Vue.nextTick(() => {
        $("#choose-title-row").niceScroll({
          cursorcolor: "rgb(99, 114, 131)",
          cursorwidth: "7px",
          cursoropacitymax: 0.4,
          cursorborder: "", // css definition for cursor border
          cursorborderradius: "",
          hidecursordelay: 1000,
        });
      });
    }

  }
</script>



